const button_menu = document.getElementById('button_menu');
const menu = document.getElementsByTagName('nav');
const left_menu = document.getElementsByClassName('left_menu');

console.log(window.innerWidth);

button_menu.addEventListener('click', (e) => {
    if(menu[0].className === 'closed_menu'){
    menu[0].className = 'open_menu';
    left_menu[0].style.transform = 'translateX(0%)';
    } else if(menu[0].className === 'open_menu'){
        menu[0].className = 'closed_menu';
        left_menu[0].style.transform = 'translateX(-100%)';
    }
})
